.flightbot__container {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
}

.flightbot__chatbox {
  display: flex;
  justify-content: center;
  margin-top: 15px;
  margin-left: 0;
  margin-right: 15px;
  min-height: 700px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.flightbot__intro {
  color: black;
  z-index: 5;
  justify-content: center;
  align-items: center;
  height: 100px;
  margin-top: 15px;
  margin-left: 15px;
}

.flightbot__intro > h1 {
  text-align: center;
}

.flightbot__intro > h2 {
  text-align: center;
  color: grey;
}

.flightbot__intro > img {
  z-index: 6;
  justify-content: center;
  display: flex;
  z-index: 10;
  margin-left: auto;
  margin-right: auto;
  width: 70%;
}
